.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

html{
  color: #707070;
}
.subtitle {
  font-size: 3.125rem;
  display: contents;
  z-index: 1000;
}
.subtitle_wrapper {
  width: fit-content;
  display: inline-block;
  font-family: bungee, sans-serif;
  font-weight: 400;
  font-style: normal;
  transform: translateY(-6px);
}
.section_title{
  line-height: 0;
  z-index: 1000;
}
.video_wrapper {
  position: relative;
  width: 50vw;
  height: 100vh;
  overflow: hidden;
}

.video_wrapper > video {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  min-width: 100%;
  min-height: 100%;
}

.video-js .vjs-play-progress {
  display: none;
}



.kv__title {
  font-size: 2.1875rem;
}
.kv__name {
  font-size: 3.4rem;
}

.kv__namewrapper {
  margin-top: 78vh;
  margin-left: 40px;
}
.kv__tree {
  width:100%;
  height:80vh;
  margin-top:24vh;
}
.kv__skew {
  font-size: 2rem;
}
.portfolio__wrapper {
  margin-top: 20vh;
  z-index: 0;
  position: absolute;
  width: 60%;
  height: 110vh;
}
.title_wrapper {
  margin-left: 80px;
  margin-top: 80px;
  height: 110vh;
}


.skill__box{
  height: 90vh;
}
.skill__wrapper {
  padding-top: 60px;
  padding-left: 120px;
}
.skill__inner_box{
  margin-left: 140px;
}


.contact__outer_box{
  height: 91vh;
  width: 99vw;
  position: absolute;
  z-index: -2;
}
.contact__title_section{
  margin-left: 80px;
  margin-top: 40vh;
  transform: skewY(-10deg);
}
.constact__content {
  padding-left: 10vw;
  padding-top: 5vh;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@media screen and (max-width: 960px) {
  .subtitle {
    font-size: 2.1rem;
  }
  .subtitle_wrapper {
    font-family: bungee, sans-serif;
    font-weight: 400;
    font-style: normal;
    transform: translateY(-6px);
    margin-top: 4vh;
  }
  .section_title{
    line-height: 0;
    z-index: 1000;
  }



  .kv__sp_background {
    position: absolute;
    z-index: 10;
  }
  .kv__namewrapper {
    margin-top: 84vh;
    margin-left: 10px;
  }
  .kv__tree {
    width: 100vw;
    height: 100vh;
    margin-top: 0;
  }
  .kv__title{
    font-size: 1.6rem;
  }
  .kv__name{
    font-size: 2.4rem;
  }
  .kv__skew {
    transform: rotate3d(0, 1, 0, 0deg);
    font-size: 1rem;
  }
  .video_wrapper {
    z-index: 1;
    position: absolute;
    width: 100%;
    height: 100vh;
  }

  .portfolio__wrapper {
    z-index: 10;
    width: 100%;
    margin-top: 41vh;
    height: inherit;
  }
  .portfolio__card_wrapper {
    margin-top: 10vh;
  }
  .title_wrapper {
    margin-left: 0;
    margin-top: 40px;
  }


  .skill__sp_top {
    margin-top: 118vh;
  }
  .skill__box{
    height: 90vh;
  }
  .skill__wrapper {
    padding-top: 0;
    padding-left: 0;
    margin-top: 70px;
  }
  .skill__inner_box{
    margin-left: 0;
    width: 100vw;
  }

  
  .contact__outer_box{
    height: 91vh;
    width: 100vw;
    position: absolute;
    margin-top: 90vh;
    word-break: break-all;
    z-index: -2;
  }
  .contact__title_section{
    margin-left: 0;
    margin-top: 40vh;
    transform: skewY(-10deg);
  }
  .constact__content {
    padding-left: 10vw;
    padding-top: 5vh;
    font-size: 1rem;
  }
}